<template>
    <div :style="'background:url('+bg+') no-repeat;background-size:cover;overflow:hidden;'">
        <Row class="write">
            <Col :xs="{ span: 10, push: 0 }" :lg="{ span: 6, push: 5 }">
                <Input v-model="filmname" size="large" placeholder="输入电影名称"></Input>
            </Col>
            <Col :xs="{ span: 10, push: 1 }" :lg="{ span: 6, push: 7 }">
                <Select placeholder="请选择电影类型" v-model.sync="type" size="large">
                    <Option v-for="item in typeList" :value="item.title" :key="item.title">{{ item.title }}</Option>
                </Select>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 10, push: 0 }" :lg="{ span: 6, push: 5 }">
                <Select placeholder="请选择电影区域" v-model.sync="area" size="large">
                    <Option v-for="item in areaList" :value="item.title" :key="item.title">{{ item.title }}</Option>
                </Select>
            </Col>
            <Col :xs="{ span: 10, push: 1 }" :lg="{ span: 6, push: 7 }">
                <Select placeholder="请选择电影年代" v-model.sync="year" size="large">
                    <Option v-for="item in yearList" :value="item.title" :key="item.title">{{ item.title }}</Option>
                </Select>
            </Col>
            <Col :xs="{ span: 1, push: 2 }" :lg="{ span: 1, push: 8 }">
                <Button @click="initData" type="ghost" shape="circle" icon="ios-search"></Button>
            </Col>
        </Row>
        <h1 class="main-title">电影列表</h1>
        <Row>
            <Col class="index-list" v-for="(item,index) in list" :key="item.id" :xs="{ span: 16, offset:8 }" :lg="{ span: 6, offset:    4 }">
                <img class="film-img" :src="item.picSrc | imagePath">
                <h3>{{item.title}}<Button @click="toBook(item)" class="index-list-btn" type="primary">订票</Button></h3>
            </Col>
        </Row>
    </div>
</template>

<script>
    import requestObj from '../../api/api';
    export default {
        name: "route",
        data(){
          return {
	          bg:require('../../assets/frontend/route-bg.jpg'),
              filmname:'',
              typeList:[{
                  title:'不限',
              },{
                  title:'剧情',
              },{
                  title:'动作',
              },{
                  title:'喜剧',
              }],
              type:'',
              areaList:[{
                  title:'不限',
              },{
                  title:'欧美',
              },{
                  title:'日韩',
              },{
                  title:'国产',
              }],
              area:'',
              yearList:[{
                  title:'不限',
              },{
                  title:'2015',
              },{
                  title:'2016',
              },{
                  title:'2017',
              },{
                  title:'2018',
              }],
              year:'',
              list: [],
          };
        },
        created(){
            this.initData();
        },
        mounted(){

        },
        methods:{
            initData(){
                var self=this;
                requestObj({
                    url:'getFilmPageInfo.do',
                    method:'GET',
                    params:{
                        filmname:self.filmname,
                        type:self.type!='不限'?self.type:'',
                        area:self.area!='不限'?self.area:'',
                        year:self.year!='不限'?self.year:'',
                        pageCurrent:1,
                        pageSize:10,
                    },
                    success:function (res){
                        var data=res.data.data.rows;
                        self.list=data;
                    },
                    fail:function (err){
                        console.log(err);
                    },
                });
            },
	        toBook(item){
		        this.$router.push('/book/'+item.id);
	        },
        },
    }
</script>

<style scoped>
    .film-img{
        width:100%;
        height:200px;
    }
</style>


